import styled from '@emotion/styled';
import { Link, useNavigate } from 'react-router-dom';

import { Container } from '@/components/common/layouts/Container';
import { useAuth } from '@/provider/Auth';
import { getDynamicPath, RouterPath } from '@/routes/path';

const backend: { [key: string]: string } = {
  backend1: 'https://api.example.com',
  backend2: 'https://api.example.com',
  backend3: 'https://api.example.com',
  backend4: 'https://api.example.com',
  backend5: 'https://api.example.com',
  backend6: 'https://api.example.com',
};

export const Header = () => {
  const navigate = useNavigate();
  const authInfo = useAuth();

  const handleLogin = () => {
    navigate(getDynamicPath.login());
  };

  return (
    <Wrapper>
      <Container flexDirection="row" alignItems="center" justifyContent="space-between">
        <Link to={RouterPath.home}>
          <Logo
            src="https://gift-s.kakaocdn.net/dn/gift/images/m640/pc_gift_logo.png"
            alt="  "
          />
        </Link>
        <RightWrapper>
          <ApiSelector>
            <option value={backend.backend1}></option>
            <option value={backend.backend2}></option>
            <option value={backend.backend3}></option>
            <option value={backend.backend4}></option>
            <option value={backend.backend5}></option>
            <option value={backend.backend6}></option>
          </ApiSelector>
          {authInfo ? (
            <LinkButton onClick={() => navigate(RouterPath.myAccount)}> </LinkButton>
          ) : (
            <LinkButton onClick={handleLogin}></LinkButton>
          )}
        </RightWrapper>
      </Container>
    </Wrapper>
  );
};

export const HEADER_HEIGHT = '54px';

export const Wrapper = styled.header`
  position: fixed;
  z-index: 9999;
  width: 100%;
  max-width: 100vw;
  height: ${HEADER_HEIGHT};
  background-color: #fff;
  padding: 0 16px;
`;

const Logo = styled.img`
  height: ${HEADER_HEIGHT};
`;
const RightWrapper = styled.div`
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 20px;
`;

const LinkButton = styled.p`
  align-items: center;
  font-size: 14px;
  color: #000;
  text-decoration: none;
  cursor: pointer;
`;

const ApiSelector = styled.select`
  width: 200px;
  height: 40px;
  border: 2px solid #444;
  border-radius: 4px;
  padding: 5px 10px;
  font-size: 16px;
  cursor: pointer;

  &:hover {
    border-color: #888;
  }
`;
